@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  margin: 0 auto;
  height: 100%;
}

img {
  object-fit: contain;
  object-position: center;
}

.button {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  padding: 10px 0;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}

.title--base {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  margin: 0;
}
@media (min-width: 768px) {
  .title--base {
    font-family: "Oswald", "Arial", sans-serif;
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
    color: #000000;
  }
}
.title--header {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  color: #111111;
  text-decoration: none;
  list-style: none;
}
.title--mini {
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  list-style: none;
  margin: 0;
}
@media (min-width: 768px) {
  .title--mini {
    font-size: 16px;
    line-height: 24px;
  }
}
.title--info {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  color: #000000;
  background-color: #ffffff;
  position: relative;
  margin: 0;
}
@media (min-width: 768px) {
  .title--info {
    font-family: "Oswald", "Arial", sans-serif;
    font-weight: 400;
    font-size: 36px;
    line-height: 36px;
  }
}
.title--text {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}
@media (min-width: 768px) {
  .title--text {
    font-family: "Oswald", "Arial", sans-serif;
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.map__interactive {
  display: block;
  border: none;
}
.map__interactive_hidden {
  display: none;
}
@media (min-width: 768px) {
  .map__interactive {
    height: 400px;
  }
}

.map__pic-container {
  display: block;
  line-height: 0;
}

@media (min-width: 768px) {
  .map__pic {
    width: 768px;
    height: 396px;
  }
}
@media (min-width: 1440px) {
  .map__pic {
    width: 1440px;
    height: 400px;
  }
}

.social__link--vkontakte {
  display: block;
  mask-image: url("../img/sprite.svg#vk");
  background-color: #666666;
  width: 28px;
  height: 22px;
}
.social__link--youtube {
  display: block;
  mask-image: url("../img/sprite.svg#youtube");
  background-color: #666666;
  width: 28px;
  height: 22px;
}
.social__link--telegram {
  display: block;
  mask-image: url("../img/sprite.svg#telegram");
  background-color: #666666;
  width: 28px;
  height: 22px;
}
.social__link:hover, .social__link:focus-visible {
  background-color: #68b738;
}
.social__link:active {
  background-color: #68b738;
  opacity: 0.3;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
