}

.page-header {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page-header {
    flex-direction: row;
    padding: 0 30px;
    justify-content: space-between;
    max-width: 768px;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .page-header {
    padding: 53px 110px 0 110px;
    background-color: transparent;
    max-width: 1440px;
    margin: 0 auto;
  }
}

.page-header__logo-container {
  display: flex;
  justify-content: space-between;
  padding: 10px 10px 12px 20px;
}
@media (min-width: 768px) {
  .page-header__logo-container {
    padding: 0 0 15px 2px;
  }
}

.page-header__logo img {
  height: 38px;
}
@media (min-width: 768px) {
  .page-header__logo img {
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1440px) {
  .page-header__logo img {
    width: 202px;
    height: 59px;
  }
}

@media (min-width: 768px) {
  .page-header__logo {
    align-items: center;
    display: flex;
  }
}
.page-header__logo:hover {
  opacity: 0.8;
}
.page-header__logo:active {
  opacity: 0.6;
}

.page-header__logo-toggle {
  border: none;
  padding: 0;
  height: 38px;
  width: 28px;
  margin-left: auto;
  background-color: #ffffff;
  cursor: pointer;
}
@media (min-width: 768px) {
  .page-header__logo-toggle {
    display: none;
  }
}

.page-header--closed .page-header__logo-toggle::before {
  content: "";
  position: absolute;
  top: 24px;
  right: 20px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.page-header--opened .page-header__logo-toggle::before, .page-header--opened .page-header__logo-toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  right: 20px;
  width: 28px;
  height: 2px;
  background-color: #000000;
}
.page-header--opened .page-header__logo-toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-header--opened .page-header__logo-toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .page-header--closed .site-nav__item {
    display: none;
  }
}
@media (max-width: 767px) {
  .page-header--opened .site-nav {
    position: absolute;
    top: 65px;
    left: 0;
    width: 100%;
    background-color: #ffffff;
    z-index: 2;
  }
}
.page-header--nojs .page-header__logo-toggle {
  display: none;
}

.page-header--nojs .site-nav__list {
  display: block;
  position: static;
  min-height: 0;
}

.page-header--nojs .site-nav__item {
  display: block;
}

.site-nav__item {
  text-transform: uppercase;
  text-align: center;
  padding-top: 17px;
  padding-bottom: 19px;
  border-top: 1px solid #e6e6e6;
}
.site-nav__item:hover {
  opacity: 0.6;
}
.site-nav__item:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
