.field-radio {
  margin: 0 -20px 23px;
  border-bottom: 1px solid $color-border-top;

  @media (min-width: $tablet-width) {
    border-bottom: none;
    margin-bottom: 78px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
  }
}

.field-radio__form {
  cursor: pointer;
}

.field-radio__input:disabled + .field-radio__label {
  cursor: default;
  opacity: 0.3;
}

.field-radio__input:disabled + .field-radio__label + .field-radio__mark {
  cursor: default;
  opacity: 0.3;
  border: 1px solid $color-grey-cd;
}

.field-radio__list {
  display: flex;
  flex-direction: column;
  row-gap: 19px;
  padding: 24px 0;

  @media (min-width: $tablet-width) {
    border: 1px solid $color-border-top;
    padding: 28px 0 25px 28px;
    row-gap: 23px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 0;
    margin-right: 0;
    border: 1px solid $color-border-top;
    width: 568px;
  }
}

.field-radio__item {
  position: relative;
  cursor: pointer;
}

.field-radio__input[type="radio"]:checked + .field-radio__label {
  &::before {
    position: absolute;
    top: 50%;
    left: 9px;
    transform: translateY(-50%);
    width: 13px;
    height: 13px;
    background-color: #68b738;
    border-radius: 50%;
    content: "";

    @media (min-width: $tablet-width) {
      width: 18px;
      height: 18px;
      left: 12px;
    }
  }
}

.field-radio__input {
  &:focus-visible ~ .field-radio__mark {
    border: 1px solid $color-grey-dark;
  }
}

.field-radio__mark {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;

  &:focus-visible {
    border: 1px solid $color-grey-dark;
  }

  @media (min-width: $tablet-width) {
    width: 40px;
    height: 40px;
  }
}

.field-radio__form:hover .field-radio__mark {
  border: 1px solid $color-form-hover;
}

.field-radio__label {
  padding-left: 45px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    padding-left: 61px;
  }
}
